@font-face {
  font-family: OpenSans;
  src: url(../../fonts/OpenSans-Italic-VariableFont_wdth\,wght.woff), url(../../fonts/OpenSans-Italic-VariableFont_wdth\,wght.woff2);
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: OpenSans;
  src: url(../../fonts/OpenSans-VariableFont_wdth\,wght.woff), url(../../fonts/OpenSans-VariableFont_wdth\,wght.woff2);
  font-style: normal;
  font-display: swap;
}
:root {
  --index: 1vw + 1vh;
}

.page {
  max-width: 100vw;
  min-height: 100%;
}

.page-body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: OpenSans, sans-serif;
  font-style: normal;
  font-weight: 300;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  min-height: 100vh;
  background: url(../../img/ratail-background.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
}

.menu__list--opened {
  display: block;
}

.menu__list--closed {
  display: none;
}

.page-header {
  width: 100%;
  display: grid;
  align-items: center;
  min-height: 48px;
  padding: 0 calc(var(--index) * 2.5);
  background-color: #000;
  border-bottom: 2px solid #fff;
}
@media (min-width: 560px) {
  .page-header {
    min-height: 65px;
  }
}

.introduction {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 1250px) {
  .introduction {
    justify-content: center;
  }
}

@media (min-width: 1250px) {
  .introduction__switches-wrapper {
    display: flex;
    align-items: center;
  }
}

.lang-switcher {
  display: flex;
  column-gap: 4px;
  padding: 7px;
}
@media (min-width: 1250px) {
  .lang-switcher {
    margin-right: calc(var(--index) * 0.5);
  }
}

.contacts {
  display: none;
}
@media (min-width: 1250px) {
  .contacts {
    display: block;
    color: #fff;
  }
}

@media (min-width: 1250px) {
  .logo {
    margin: 0 auto;
  }
}

.logo__image {
  height: calc(var(--index) * 5);
}
@media (min-width: 560px) {
  .logo__image {
    height: calc(var(--index) * 5);
  }
}
@media (min-width: 1250px) {
  .logo__image {
    width: calc(var(--index) * 25);
    height: auto;
  }
}
@media (min-width: 1920px) {
  .logo__image {
    width: calc(var(--index) * 25);
    height: auto;
  }
}

.quote {
  display: none;
}
@media (min-width: 1250px) {
  .quote {
    display: block;
    color: #fff;
    font-size: 14px;
  }
}

@media (min-width: 1250px) {
  .menu-toggle {
    display: none;
  }
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: calc(var(--index) * 2) 0;
}
@media (min-width: 1250px) {
  .menu__list {
    width: 100%;
    flex-direction: row;
    justify-content: center;
    padding: 10px;
  }
}

@media (min-width: 1250px) {
  .dropdown {
    display: none;
  }
}

.list__item {
  font-size: calc(var(--index) * 2);
  color: #fff;
}
@media (min-width: 860px) {
  .list__item {
    font-size: calc(var(--index) * 0.4);
    margin-right: calc(var(--index) * 0.5);
  }
}
@media (min-width: 1250px) {
  .list__item {
    font-size: calc(var(--index) * 0.2);
    margin-right: calc(var(--index) * 1);
  }
}
@media (min-width: 1920px) {
  .list__item {
    font-size: calc(var(--index) * 0.05);
  }
}

.page-main {
  flex: 1 1 auto;
}

.page-main__wrapper {
  min-height: 100vh;
  position: relative;
}

.opening-text {
  padding: 60px 0 90px 0;
  color: #fff;
}
@media (min-width: 860px) {
  .opening-text {
    position: absolute;
    top: calc(var(--index) * 30);
    left: 0;
  }
}

.opening-text__field {
  margin-bottom: 15px;
}

.opening-text__text {
  display: inline-block;
  padding-left: calc(var(--index) * 8);
  font-size: calc(var(--index) * 3);
  border-bottom: 1px solid #fff;
}
@media (min-width: 560px) {
  .opening-text__text {
    font-size: calc(var(--index) * 5);
  }
}
@media (min-width: 860px) {
  .opening-text__text {
    font-size: calc(var(--index) * 2);
  }
}
@media (min-width: 1920px) {
  .opening-text__text {
    font-size: calc(var(--index) * 3);
  }
}

.gallery {
  width: 100%;
  min-height: 100vh;
}

.gallery__wrapper {
  position: relative;
}

.gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 560px) {
  .gallery__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
@media (min-width: 860px) {
  .gallery__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(3, 300px);
    gap: 25px;
    position: absolute;
    top: calc(var(--index) * 10);
    left: 43vw;
  }
}
@media (min-width: 1920px) {
  .gallery__list {
    left: 50vw;
  }
}

.gallery__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: calc(var(--index) * 30);
  background: url(../../img/retail-background.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}
.gallery__item:nth-child(2) {
  background: url(../../img/security_img.jpg);
  background-position: center;
  background-size: cover;
}
.gallery__item:nth-child(3) {
  background: url(../../img/IMG_5294.WEBP);
  background-position: center;
  background-size: cover;
}
.gallery__item:nth-child(4) {
  background: url(../../img/list-item_bg.jpg);
  background-position: center;
  background-size: cover;
}
.gallery__item:nth-child(5) {
  background: url(../../img/IMG_5296.JPG);
  background-position: center;
  background-size: cover;
}
.gallery__item:nth-child(6) {
  background: url(../../img/construction_img.jpg);
  background-position: center;
  background-size: cover;
}
@media (min-width: 560px) {
  .gallery__item {
    width: 50%;
    min-height: calc(var(--index) * 35);
  }
}
@media (min-width: 860px) {
  .gallery__item {
    min-width: calc(var(--index) * 30);
    min-height: calc(var(--index) * 15);
  }
}
@media (min-width: 1250px) {
  .gallery__item {
    min-width: calc(var(--index) * 40);
    min-height: calc(var(--index) * 20);
  }
}
@media (min-width: 1920px) {
  .gallery__item {
    min-width: calc(var(--index) * 40);
    min-height: calc(var(--index) * 19);
  }
}

.gallery__text {
  color: #fff;
  font-size: calc(var(--index) * 2.5);
  opacity: 1;
}
@media (min-width: 860px) {
  .gallery__text {
    font-size: calc(var(--index) / 2);
  }
}

.page-footer {
  width: 100%;
  min-height: 150px;
  background-color: #000;
}

@media (min-width: 860px) {
  .about_us__opening-text {
    top: calc(var(--index) * 70);
  }
}

.about_us__description {
  color: white;
}

@media (min-width: 860px) {
  .code_of_ethics__opening-text {
    top: calc(var(--index) * 70);
  }
}

.code_of_ethics__description {
  color: white;
}

.code_of_ethics__dropdown {
  color: white;
}